import { InputCommon } from '../../../common/input';
import wvc from '../../../../public/img/w_vc.png';
import { useEffect, useState } from 'react';
import Button from '@mui/material/Button';
import { Result } from '../../../common/result';
import Grid from '@mui/material/Grid';

export const MillingCuttingSpeed = () => {
    const [result, setResult] = useState(0);
    const [b, setB] = useState(0);
    const [kc, setKc] = useState(0);
    const [kv, setKv] = useState(0);
    const [kver, setKver] = useState(0);
    const [kst, setKst] = useState(0);
    const [kg, setKg] = useState(0);
    const [vc, setVc] = useState(0);
    const [p, setP] = useState(0);
    const [n, setN] = useState(0);
    const [n2, setN2] = useState(0);
    const [z, setZ] = useState(0);
    const [b2, setB2] = useState(0);
    const [d, setD] = useState(0);
    const [fz, setFz] = useState(0);
    const [p2, setP2] = useState(0);
    const [reset, setReset] = useState(false);

    const handleOnClick = () => {
        setResult(0);
        document.getElementById('phay3').reset();
        setReset(true);
    };

    useEffect(() => {
        const hm = ((114.6) * b2 * fz * Math.sin(p2) ) / (p * d);
        const pResult = (b * hm * kc * kv * kver * kst * vc * p * n) / (60 * Math.pow(10, 3) * n2 * Math.pow(hm, z) * 360);
        setResult(pResult);
    }, [b, kc, kver, kst, kv, kg, vc, p, n, n2, z, b2, d, fz, p2]);

    return (
        <form id="phay3">
            <div style={{ display: 'flex', flexDirection: 'column' }}>
                <Grid container spacing={2} style={{ width: '100%' }}>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="B rng vt ct: b (mm)" placeholder="Nhp thng s" setValue={setB}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="Lc ct n v: kc1.1 (N/mm2)" placeholder="Nhp thng s" setValue={setKc}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="H s Kg" placeholder="Nhp thng s" setValue={setKg}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="H s Kv" placeholder="Nhp thng s" setValue={setKv}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="H s Kver" placeholder="Nhp thng s" setValue={setKver}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="H s Kst" placeholder="Nhp thng s" setValue={setKst}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="Tc  ct: Vc (m/ph)" placeholder="Nhp thng s" setValue={setVc}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="Gc tip cn: s" placeholder="Nhp thng s" setValue={setP}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="S li dao ct: n" placeholder="Nhp thng s" setValue={setN}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="Hiu sut ng c:  (70  80%)" placeholder="Nhp thng s" setValue={setN2}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="Hng s vt liu: z" placeholder="Nhp thng s" setValue={setZ}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="Chiu rng dao phay: B(mm)" placeholder="Nhp thng s" setValue={setB2}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="ng knh dao phay: D(mm)" placeholder="Nhp thng s" setValue={setD}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="Lng chy dao rng: fz (mm/rng)" placeholder="Nhp thng s" setValue={setFz}/>
                    </Grid>
                    <Grid item sm={12} md={6} xl={3} lg={4}>
                         <InputCommon text="Gc bo: " placeholder="Nhp thng s" setValue={setP2}/>
                    </Grid>
                </Grid>
                <Result src={wvc} result={result} unit="m/min" setValue={setResult} />
                <br></br>
                <Button variant="outlined" style={{ margin: '0 auto' }} onClick={handleOnClick} >Reset</Button>
            </div>
        </form>
    );
};