import { InputCommon } from '../../common/input';
import wf from '../../../public/img/w_f.png';
import { useEffect, useState } from 'react';
import Button from '@mui/material/Button';
import { Result } from '../../common/result';
import Grid from '@mui/material/Grid';
import { Selector } from '../../common/selector';
import { y0Items, kvItems, kstItems, kcItems, zItems } from './data';

export const PowerCut = () => {
    const [result, setResult] = useState(0);
    const [ap, setAp] = useState(0);
    const [f, setF] = useState(0);
    const [kc, setKc] = useState(0);
    const [a, setA] = useState(0);
    const [yt, setYt] = useState(0);
    const [y0, setY0] = useState(0);
    const [kv, setKv] = useState(0);
    const [kver, setKver] = useState(1.3);
    const [kst, setKst] = useState(0);
    const [z, setZ] = useState(0);
    const [reset, setReset] = useState(false);

    const handleOnClick = () => {
        setResult(0);
        document.getElementById('tien3').reset();
        setReset(true);
    };

    useEffect(() => {
        const fc = ap * f * (kc / (f * Math.pow(Math.sin(a)), 2)) * (1 - (yt - y0) / 100) * kv * ( kst * kver);
        setResult(fc);
    }, [ap, f, kc, a, yt, y0, kv, kst, z]);

    return (
        <form id="tien3">
            <div style={{ display: 'flex', flexDirection: 'column' }}>
            <Grid container spacing={2}>
                    <Grid item xs={12} xl={3} lg={4}> 
                        <InputCommon text="Chiu su ct: ap(mm)" placeholder="Nhp thng s" setValue={setAp}/>
                    </Grid>
                    <Grid item xs={12} xl={3} lg={4}>
                        <InputCommon text="Lng chy dao: f(mm/vng)" placeholder="Nhp thng s" setValue={setF}/>
                    </Grid>
                    <Grid item xs={12} xl={3} lg={4}>  
                        <Selector text="Lc ct n v: kc1.1(N/mm2)" defaultLabel="Nhp thng s" disable={true} setValue={setKc} items={kcItems}/>
                    </Grid>
                    <Grid item xs={12} xl={3} lg={4}>
                        <InputCommon text="Gc ct: " placeholder="Nhp thng s" setValue={setA}/>
                    </Grid>
                    <Grid item xs={12} xl={3} lg={4}>
                        <InputCommon text="Gc trc thc t: tat" placeholder="Nhp thng s" setValue={setYt}/>
                    </Grid>
                    <Grid item xs={12} xl={3} lg={4}> 
                        <Selector text="Gc trc: 0" defaultLabel="Chn thng s" disable={true} setValue={setY0} items={y0Items}/>
                    </Grid>
                    <Grid item xs={12} xl={3} lg={4}>
                        <Selector text="H s hiu chnh tc  ct: Kv" defaultLabel="Chn thng s" disable={true} setValue={setKv} items={kvItems}/>
                    </Grid>
                    <Grid item xs={12} xl={3} lg={4}>
                        <InputCommon text="H s Kver" disable={true} setValue={setKver} defaultValue={kver}/>
                    </Grid>
                    <Grid item xs={12} xl={3} lg={4}>
                        <Selector text="H s nn: Kst" defaultLabel="Chn thng s" setValue={setKst} items={kstItems} disable={true}/>
                    </Grid>
                    <Grid item xs={12} xl={3} lg={4}>
                        <Selector text="Hng s vt liu: z" defaultLabel="Chn thng s" setValue={setZ} items={zItems} disable={true}/>
                    </Grid>
                </Grid>
                <Result src={wf} result={result} unit="mm/rev" setValue={setResult} />
                <br></br>
                <Button variant="outlined" style={{ margin: '0 auto' }} onClick={handleOnClick} >Reset</Button>
            </div>
        </form>
    );
};